<template>
  <div class="home">
    <MglMap
      class="map"
      :accessToken="accessToken"
      :mapStyle="mapStyle"
      :center="center"
      :zoom.sync="zoom"
      @load="onMapLoaded"
    >
      <MglNavigationControl position="top-right"/>
    </MglMap>
    <div v-if="mapLoaded">
      <div v-if="roadLayerSwitch">
        <div v-for="(roadMarker, name) of roadMarkers" :key="name">
          <MarkerRoad v-if="zoom >= 3.8 && zoom <= 8"
            :roadName="roadMarker.roadName"
            :lngLat="roadMarker.lngLat"
            :shortName="roadMarker.shortName"
            :responseData="roadMarker.responseData"
          />
        </div>
      </div>

      <div v-for="(marker, name) in stationMarkers" :key="name">
        <MarkerStation v-if="zoom >= 8"
          :stationName="marker.stationName"
          :lngLat="marker.lngLat"
          :responseData="marker.responseData"
        />
      </div>

      <DialogError v-model="errorLoadingData"/>

      <v-card
        class="overlay"
        width="350"
        rounded
        elevation="3"
      >
        <v-text-field
          v-model="stationSearch"
          hide-details
          prepend-inner-icon="mdi-magnify"
          solo
          label="Найти станцию"
        ></v-text-field>
        <v-list-item
          two-line
          v-for="(station, i) in filteredStations"
          :key="i"
          @click="goToStation(station.lngLat)"
        >
          <v-list-item-content>
            <v-list-item-title>{{ station.name }}</v-list-item-title>
            <v-list-item-subtitle>{{ station.railwayName }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-card>
    </div>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Загрузка
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-btn
      class='floatingButton'
      elevation="3"
      fab
    >
      <v-icon @click='layersFilter = !layersFilter'>mdi-cog</v-icon>
    </v-btn>
    <v-navigation-drawer
      v-model="layersFilter"
      absolute
      temporary
    >
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title><b>Настройка слоёв</b></v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list-item>
        <v-list-item-content>
          <v-switch
            v-model="streamLayerSwitch"
            class='filterSwitch'
            label="Вагоно-потоки"
            @change="switchStreams()"
          ></v-switch>
          <v-switch
            v-model="roadLayerSwitch"
            class='filterSwitch'
            label="Показатели по дороге"
          ></v-switch>
        </v-list-item-content>
      </v-list-item>
    </v-navigation-drawer>
    <v-btn
      class='errorButton'
      elevation="3"
      fab
    >
      <v-icon @click='openDialogError()'>mdi-reload</v-icon>
    </v-btn>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
// import Mapbox from 'mapbox-gl'
import { MglMap, MglNavigationControl } from 'vue-mapbox'
import mapStyle from '@/assets/map-style.json'
import styleVariables from '@/css/variables.sass'
import MarkerRoad from '@/components/MarkerRoad'
import MarkerStation from '@/components/MarkerStation'
import DialogError from '@/components/DialogError'

import railwayJSON from '@/assets/StationVP_Planning.json'
// import railwayJSON from '@/assets/testGis.json'
// import railwayJSON from '@/assets/target-file.json'
// import stationsFromJSON from '@/assets/osm2esr.json'

mapStyle.sources.openmaptiles.url = `http://${location.hostname}:8080/data/v3.json`
mapStyle.sprite = `http://${location.hostname}:8081/sprite`
mapStyle.glyphs = `http://${location.hostname}:8081/fonts/{fontstack}/{range}.pbf?key=HPYnh52aKQ0gUiQdaKKM`

export default {
  name: 'Home',
  components: {
    MglMap,
    MglNavigationControl,
    MarkerRoad,
    MarkerStation,
    DialogError
  },

  data: () => ({
    accessToken: 'pk.eyJ1IjoiY2hpZ2EiLCJhIjoiY2tqYnRyN2NzN3B4ZTJxcWpvdmVldGdmYSJ9.mTIH5Hby4yDyU-LLFOY0Fw',
    mapStyle: mapStyle,
    zoom: 3,
    mapLoaded: false,
    center: [83.214, 60.703],
    loading: false,
    stationSearch: null,
    railway: {
      roads: [{
        name: null,
        code: null,
        shortName: null,
        stations: [{
          esr: null,
          name: null,
          lngLat: [null, null],
          threads: [{
            esr: null,
            lngLat: [],
            stream: null,
            points: []
          }]
        }]
      }]
    },
    streamLines: [],
    layersFilter: null,
    streamLayerSwitch: true,
    roadLayerSwitch: true,
    errorLoadingData: false,
    roadMarkers: [{
      roadName: null,
      shortName: null,
      lngLat: []
    }],
    stationMarkers: {},
    allStationsMap: {}
  }),

  methods: {
    onMapLoaded: function (event) {
      this.$store.map = event.map
      this.mapLoaded = true
      this.railway = railwayJSON
      this.getMarkerRoadData()
      this.createRailAreas()
      this.createStreamLines()
      this.createStations()
      this.getMarkerStationData()
      const map = this.$store.map
      // click and hover on station
      map.on('click', 'station-point', (event) => {
        this.goToStation(event.features[0].geometry.coordinates) //
      })
      map.on('mouseenter', 'station-point', function () {
        map.getCanvas().style.cursor = 'pointer'
      })
      map.on('mouseleave', 'station-point', function () {
        map.getCanvas().style.cursor = ''
      })
      map.on('moveend', () => {
        if (this.zoom > 7) {
          const features = map.queryRenderedFeatures({ layers: ['station-point'] })
          // let updatedPopups = []
          for (const feature of features) {
            const id = feature.id
            // const coordinates = feature.geometry.coordinates
            // console.log(`moveend: zoom > 7: feature esr type = ${typeof feature.properties.esr}`)
            // console.log(`moveend: zoom > 7: feature esr = ${feature.properties.esr}`)
            const reqEsr = feature.properties.esr
            if (!Object.prototype.hasOwnProperty.call(this.stationMarkers, id)) {
              this.stationMarkers[reqEsr] = {
                stationName: this.allStationsMap[reqEsr].name,
                lngLat: this.allStationsMap[reqEsr].lngLat,
                responseData: this.allStationsMap[reqEsr].responseData
              }
            }
          }
        } else {
          this.stationMarkers = {}
        }
      })
    },
    createRailAreas () {
      this.$store.map.addSource('rail-areas', {
        type: 'geojson',
        data: require('@/assets/railAreas.json')
      })
      this.$store.map.addLayer({
        id: 'rail-polygons',
        type: 'fill',
        source: 'rail-areas',
        layout: {},
        maxzoom: 6,
        paint: {
          'fill-color': [
            'match', ['get', 'code'],
            [10, 24, 61, 83, 96], styleVariables.railArea1,
            [28, 41, 63, 88], styleVariables.railArea2,
            [17, 51, 80, 92], styleVariables.railArea3,
            [1, 58, 76, 94], styleVariables.railArea4,
            /* other */ 'grey'
          ],
          'fill-opacity': [
            'interpolate', ['linear'], ['zoom'],
            0, 0.7,
            2, 0.20,
            5, 0.20,
            6, 0
          ]
        }
      })
      this.$store.map.addLayer({
        id: 'rail-borders',
        type: 'line',
        source: 'rail-areas',
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        minzoom: 5.5,
        paint: {
          'line-color': styleVariables.railBorder,
          'line-width': 2,
          'line-opacity': [
            'interpolate', ['linear'], ['zoom'],
            5.5, 0,
            6.5, 0.6
          ]
        }
      })
    },
    createStations () {
      this.$store.map.addSource('stations', {
        type: 'geojson',
        data: {
          type: 'FeatureCollection',
          features: []
        }
      })

      this.$store.map.addLayer({
        id: 'station-point',
        type: 'circle',
        source: 'stations',
        minzoom: 3.8,
        paint: {
          'circle-stroke-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8, 0,
            4, 1
          ],
          'circle-radius': {
            stops: [
              [3, 2.5],
              [6, 5],
              [12, 7]
            ]
          },
          'circle-color': 'black',
          // 'circle-color': [
          //   // // 'green'
          //   // 'interpolate',
          //   // ['linear'],
          //   // ['get', 'load'],
          //   // -1, 'grey',
          //   // 0, 'green',
          //   // 0.5, 'yellow',
          //   // 1, 'red',
          //   // 1.01, '#800000'
          // ],
          'circle-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8, 0,
            4, 1
          ]
        }
      })

      this.$store.map.addLayer({
        id: 'stations-labels',
        type: 'symbol',
        source: 'stations',
        minzoom: 6,
        maxzoom: 8,
        layout: {
          'text-field': ['get', 'name'],
          'text-font': [
            'Metropolis Light',
            'Noto Sans Regular'
          ],
          'text-anchor': 'left',
          'text-offset': [0.8, 0],
          'text-size': 15
        },
        paint: {
          'text-halo-width': 1,
          'text-color': '#646464'
        }
      })
    },
    createStreamLines () {
      const streamsJson = {
        type: 'FeatureCollection',
        features: []
      }
      for (const road of this.railway.roads) {
        if (road.stations.length > 0) {
          for (const st of road.stations) {
            for (const thread of st.threads) {
              if (thread.points.length > 0) {
                streamsJson.features.push({
                  type: 'Feature',
                  geometry: {
                    type: 'LineString',
                    coordinates: thread.points
                  },
                  properties: {
                    width: thread.stream
                  }
                })
              }
            }
          }
        }
      }
      this.$store.map.addSource('streams', {
        type: 'geojson',
        data: streamsJson
      })

      this.$store.map.addLayer({
        id: 'stream',
        type: 'line',
        source: 'streams',
        minzoom: 3.8,
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        paint: {
          'line-color': '#33cc33',
          'line-width': [
            'step', ['get', 'width'],
            1,
            100,
            3,
            200,
            5,
            300,
            7,
            400,
            9
          ]
        }
      })

      this.$store.map.addLayer({
        id: 'streamText',
        type: 'symbol',
        source: 'streams',
        minzoom: 3.8,
        layout: {
          'symbol-placement': 'line',
          'text-font': [
            'Metropolis Light',
            'Noto Sans Regular'
          ],
          'text-field': '{width}',
          'text-size': 12
        },
        paint: {}
      })
    },
    getRoadsGeoJSON () {
      const stationsFeaturesAllRoads = this.railway.roads.reduce((accumulator, road) => {
        if (road.stations.length > 0) {
          const stationsGeoJSON = this.getStationsGeoJSON(road)
          accumulator = accumulator.concat(stationsGeoJSON.features)
        }
        return accumulator
      }, [])

      return {
        type: 'FeatureCollection',
        features: stationsFeaturesAllRoads
      }
    },
    getStationsGeoJSON (road) {
      const stationsFeatures = road.stations.map(station => {
        return {
          type: 'Feature',
          id: station.esr,
          properties: {
            name: station.name,
            esr: station.esr,
            roadCode: road.code
          },
          geometry: {
            type: 'Point',
            coordinates: station.lngLat
          }
        }
      })
      return {
        type: 'FeatureCollection',
        features: stationsFeatures
      }
    },
    goToStation (lngLat) {
      this.$store.map.flyTo({
        center: lngLat,
        zoom: 8,
        bearing: 0,
        speed: 2,
        essential: true
      })
      this.stationSearch = ''
    },
    switchStreams () {
      const map = this.$store.map
      if (this.streamLayerSwitch) {
        map.setLayoutProperty('stream', 'visibility', 'visible')
      } else {
        map.setLayoutProperty('stream', 'visibility', 'none')
      }
    },
    async getMarkerRoadData () {
      this.loading = true
      // const today = new Date()
      // const paramDate = today.toISOString().slice(0, 10).replace(/-/g, '')
      try {
        const { data } = await axios.get('ajax2.php', {
          params: {
            module: 'TKP',
            method: 'tkp_dimensions_cutr'
          }
        })
        this.railway.roads.map(road => {
          if (road.code in data.data) {
            road.responseData = data.data[road.code]
          }
        })
        this.updateRoadMarkers()
      } catch (e) {
        console.log('Can\'t load RoadMarkers data. getMarkerRoadData(): ')
        console.log(e)
        this.openDialogError()
      }
      this.loading = false
    },
    updateRoadMarkers () {
      const coordinatesMap = {
        88: [93.449533, 55.668329],
        92: [108.191104, 53.626679],
        94: [119.502052, 55.197234],
        96: [138.168995, 48.295739]
      }
      this.roadMarkers = []
      for (const road of this.railway.roads) {
        this.roadMarkers.push({
          roadName: road.name,
          lngLat: coordinatesMap[road.code],
          shortName: road.shortName,
          responseData: road.responseData
        })
      }
    },
    async getMarkerStationData () {
      this.loading = true
      console.log('getMarkerStationData : START')
      try {
        const { data } = await axios.get('ajax2.php', {
          params: {
            module: 'TKP',
            method: 'station',
            tablo: 'vp'
          }
        })

        const dataMap = {}
        data.data.forEach(item => {
          dataMap[item.esr] = item
        })

        this.railway.roads.map(road => {
          road.stations.map(station => {
            const reqStationEsr = station.esr.toString().length > 5 ? Math.trunc(station.esr / 10) : station.esr
            station.responseData = dataMap[reqStationEsr] ? dataMap[reqStationEsr] : null
          })
        })

        console.log('getMarkerStationData : request complete')
        this.updateAllStationsMap()
        // this.updateStationMarkers()
      } catch (e) {
        console.log('Can\'t load StationMarkers data. getMarkerRoadData(): ')
        console.log(e)
        this.openDialogError()
      }
      this.loading = false
    },
    updateAllStationsMap () {
      this.railway.roads.map(road => {
        road.stations.map(station => {
          this.allStationsMap[station.esr] = station
        })
      })
    },
    // updateStationMarkers () {
    //   this.stationMarkers = []
    //   for (const road of this.railway.roads) {
    //     for (const station of road.stations) {
    //       this.stationMarkers.push({
    //         stationName: station.name,
    //         lngLat: station.lngLat,
    //         responseData: station.responseData
    //       })
    //     }
    //   }
    // },
    openDialogError () {
      this.errorLoadingData = true
    }
  },
  computed: {
    filteredStations () {
      if (!this.stationSearch) return []
      const a = this.railway.roads
        // add railway name to station
        .map(road => {
          const railwayName = road.name
          const roadCode = road.code
          road.stations = road.stations.map(station => {
            return {
              ...station,
              railwayName,
              roadCode
            }
          })
          return road
        })
        // take all stations
        .reduce((accumulator, road) => accumulator.concat(road.stations), [])
        // filter by user input
        .filter(station => station.name.toLowerCase().indexOf(this.stationSearch.toLowerCase()) === 0)
        // sort by alphabet
        .sort((a, b) => a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1)
        // slice first 10 elements
        .slice(0, 10)
      // console.log(a)
      return a
    }
  },
  watch: {
    railway: {
      handler: function () {
        const roadsGeoJSON = this.getRoadsGeoJSON()
        // console.log(roadsGeoJSON)
        this.$store.map.getSource('stations').setData(roadsGeoJSON)
        // this.updateRoadMarkers()
      },
      deep: true
    }
  }
}
</script>

<style scoped lang="sass">
.map
  position: absolute
  top: 0
  bottom: 0
  width: 100%

.overlay
  position: absolute
  user-select: none
  margin: 10px

.floatingButton
  box-shadow: 0 0 0 2px rgba(0,0,0,.1) !important
  position: absolute
  left: 0
  top: 0
  margin: 10px 370px
  border-radius: 4px !important
  width: 29px !important
  height: 48px !important

.errorButton
  box-shadow: 0 0 0 2px rgba(0,0,0,.1) !important
  position: absolute
  right: 0
  top: 0
  margin: 110px 10px
  border-radius: 4px !important
  width: 29px !important
  height: 29px !important

.filterSwitch
  margin-left: 10px !important
</style>
